import React, { useState } from 'react';
import { Button, Collapse } from 'react-bootstrap';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import logo from "../logo1.svg";
import "./Compiler/Home.css";
import "./Compiler/Compiler.css";

export default function Navbar(props) {
	const [open, setOpen] = useState(false);



	return (
		<>
			<head>
				<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap@4.4.1/dist/css/bootstrap.min.css" integrity="sha384-Vkoo8x4CGsO3+Hhxv8T/Q5PaXtkKtu6ug5TOeNV6gBiFeWPGFN9MuhOf23Q9Ifjh" crossorigin="anonymous"></link>
				<script src="https://code.jquery.com/jquery-3.4.1.slim.min.js" integrity="sha384-J6qa4849blE2+poT4WnyKhv5vZF5SrPo0iEjwBvKU7imGFAV0wwj1yYfoRSJoZ+n" crossorigin="anonymous"></script>
				<script src="https://cdn.jsdelivr.net/npm/popper.js@1.16.0/dist/umd/popper.min.js" integrity="sha384-Q6E9RHvbIyZFJoft+2mJbHaEWldlvI9IOYy5n3zV9zzTtmI3UksdQRVvoxMfooAo" crossorigin="anonymous"></script>
				<script src="https://cdn.jsdelivr.net/npm/bootstrap@4.4.1/dist/js/bootstrap.min.js" integrity="sha384-wfSDF2E50Y2D1uUdj0O3uMBJnjuUD4Ih7YwaYd1iqfktj0Uod8GCExl3Og8ifwB6" crossorigin="anonymous"></script>
			</head>
			<nav className={` navbar-expand-lg navbar-dark bg-dark line`}>
				{/* <div className='logos'>
					<img src={logo} className="logo" alt='mlogo' />

				</div> */}
				<div className="container-fluid ">

					<Button
						onClick={() => setOpen(!open)}
						aria-controls="example-collapse-text"
						aria-expanded={open} className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" ><span className="navbar-toggler-icon"></span>
					</Button>

					<Collapse in={open}>
						<div className="">
							<div className='logos'>
								<img src={logo} className="logo" alt='mlogo' />

							</div>
							<ul className="navbar-nav me-auto mb-2 mb-lg-0">
								<li className="nav-item"><Link className="navbar-brand active" to="/">{props.title}</Link></li>
								<li className="nav-item"><Link className="nav-link active" aria-current="page" to="/about">{props.aboutText}</Link></li>
								<li className="nav-item"><Link className="nav-link active" aria-current="page" to="/contact">{props.contactText}</Link></li>
								<li className="nav-item"><Link className="nav-link active" aria-current="page" to="/help">{props.helpText}</Link></li>
								<li className="nav-item"><Link className="nav-link active" aria-current="page" to="/compiler" >{props.compileText}</Link></li>
								<li className="nav-item"><Link className="nav-link active" aria-current="true" to="/textform">{props.textAnalyzer}</Link></li>
								<li className="nav-item"><a className='nav-link active' href="https://www.codewithmaurya.com/home/blog/" >Blog</a></li>
							</ul>
							<form className="dist text-right" >
								<button className="btn btn-dark mx-2" type="button"><Link to="/contact" >Signup</Link></button>
								<button className="btn btn-dark mx-2" type="button"><Link to="/login" >Login</Link></button>
							</form>
						</div>
					</Collapse>
				</div>

			</nav>
		</>
	)
}

Navbar.propTypes = {
	aboutText: PropTypes.string.isRequired,
	contactText: PropTypes.string.isRequired,
	helpText: PropTypes.string
}
Navbar.defaultProps = {
	title: 'set title here',
	aboutText: 'set about here'
}