import React, { Component } from "react";
import "./App.css";
import "./pages/Css.css";
import Compiler from "./components/Compiler/Compiler";
import Navbar from "./components/Navbar";
import About from "./pages/About";
import Home from "./components/Compiler/Home";
import TextForm from "./components/TextForm";
// import Alert from "./pages/Alert";
import Contact from "./pages/Contact";
import Help from "./pages/Help";
import Login from "./pages/Login";

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Footer from "./pages/Footer";

export default class App extends Component {

  render() {
    return (
      <>
        <Router>
          <Navbar title="Home" aboutText="About us" contactText="Contact us" helpText="Help" compileText="IDE" textAnalyzer="Text Analyzer" />
          {/* <Alert alert="This is alert" /> */}
          <Switch>
            {/* <div className="scrllable"> */}
            <Route exact path="/About"><About /></Route>
            <Route exact path="/Contact"><Contact /></Route>
            <Route exact path="/"><Home /></Route>
            <Route exact path="/TextForm"><TextForm heading="Enter Text To Analyze" /></Route>
            <Route exact path="/Help"><Help /></Route>
            <Route exact path="/Compiler"><Compiler /></Route>
            <Route exact path="/Contact"><Contact /></Route>
            <Route exact path="/login"><Login /></Route>

            {/* </div> */}
          </Switch>

          <Footer className="footer" />

        </Router>

      </>
    );
  }
}
