import React, { useState } from 'react';

import { useSpeechSynthesis } from "react-speech-kit";



export default function TextForm(props) {

    const { speak } = useSpeechSynthesis();





    const handleUpClick = () => {
        // console.log("Uppercase was clicked");
        let newText = text.toUpperCase();
        setText(newText);

    }
    const handleClearClick = () => {
        // console.log("Uppercase was clicked");

        setText("");

    }
    const handleLowClick = () => {
        // console.log("Uppercase was clicked");
        let newText = text.toLowerCase();
        setText(newText);

    }
    const handleOnChange = (event) => {
        setText(event.target.value);
    }
    const searchWord = (event) => {
        rvsetText(event.target.value);

    }
    const replaceWord = (event) => {
        rssetText(event.target.value);


    }
    const handleCopy = () => {
        var text = document.getElementById("myBox");
        text.select();
        text.setSelectionRange(0, text.length);
        navigator.clipboard.writeText(text.value);
    }
    const handleReplaceClick = () => {
        let newText = text.replaceAll(rtext, rsetText);
        setText(newText);


    }
    const handleExtraSpaces = () => {
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "));
    }
    const [rtext, rvsetText] = useState('');
    const [rsetText, rssetText] = useState('');
    const [text, setText] = useState('');
    return (
        <>
            <div className='container'>
                <h1>{props.heading} </h1>

                <div className="mb-3">
                    <div className="d-flex">
                        <p>Enter text to replace</p>
                        <input type="text" onChange={searchWord} value={rtext} />
                        <p>Replced by</p>
                        <input type="text" onChange={replaceWord} value={rsetText} />
                        <button className="btn btn-primary mx-1" onClick={handleReplaceClick} >Replace Word</button>
                    </div>
                    <label htmlFor="myBox" className="form-label">Enter text here to analyse</label>
                    <textarea className="form-control" onChange={handleOnChange} value={text} id="myBox" rows="10"></textarea>
                </div>
                <button className="btn btn-primary mx-1" onClick={() => speak({ text: text })} >text to speak</button>
                <button className="btn btn-primary mx-1" onClick={handleUpClick} >Convert To Uppercase</button>
                <button className="btn btn-primary mx-1" onClick={handleLowClick} >Convert To Lowercase</button>
                <button className="btn btn-primary mx-1" onClick={handleCopy} >Copy</button>
                <button className="btn btn-primary mx-1" onClick={handleClearClick} >Clear</button>
                <button className="btn btn-primary mx-1" onClick={handleExtraSpaces} >Remove Extra Spaces</button>
            </div>
            <div className='container'>
                <h2>Your Text Summary</h2>
                <p>{text.split(" ").length} words and {text.length} characters</p>
                <p>Reading time of availabel test is {0.008 * text.split(" ").length}</p>
                <h2>Preview</h2>
                <p>{text}</p>
            </div>

        </>
    );
}
